@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.threed {
  -webkit-transform: perspective(600px) rotateX(60deg);
  transform: perspective(600px) rotateX(60deg);
}

.bigHexagon {
  position: absolute;
  top: 0;
  left: 50;
  margin-left: -150px;
  z-index: -100;
  display: inline-block;
}

.bigHexagonTop {
  float: left;
  margin: 2px;
  margin-right: 0px;
  width: 0;
  border-right: 400px solid #6C6;
  border-top: 475px solid transparent;
  border-bottom: 475px solid transparent;
}

.bigHexagonMiddle {
  float: left;
  margin: 2px;
  margin-right: 0px;
  margin-left: 0px;
  width: 600px;
  height: 950px;
  background-color: #6C6;
}

.bigHexagonBottom {
  float: left;
  margin: 2px;
  width: 0;
  margin-left: 0px;
  border-left: 400px solid #6C6;
  border-top: 475px solid transparent;
  border-bottom: 475px solid transparent;
}

.changeColor {
  display: none;
}

.break {
  flex-basis: 100%;
  height: 0;
}

.harbor {
  margin: 2px;
  width: 60px;
  height: 40px;
  text-align: 'center';
  background-color: #efefc4;
}
.hexagonTop {
    margin: 2px;
    margin-bottom: 0px;
    width: 0px;
    border-bottom: 50px solid #6C6;
    border-left: 87px solid transparent;
    border-right: 87px solid transparent;
}
.hexagonMiddle {
    margin: 2px;
    margin-top: 0px;
    margin-bottom: 0px;
    width: 174px;
    height: 100px;
    background-color: #6C6;
}
.hexagonBottom {
    margin: 2px;
    width: 0px;
    margin-top: 0px;
    border-top: 50px solid #6C6;
    border-left: 87px solid transparent;
    border-right: 87px solid transparent;
}
.number {
    margin: 2px;
    width: 60px;
    height: 60px;
    text-align: 'center';
    background-color: #efefc4;
    margin-top: 0px;
    border-radius: 50%;
    position: relative;
    top: 50%;
    -webkit-transform: perspective(1px) translateY(-50%);
            transform: perspective(1px) translateY(-50%);
}
.robber {
    height: 60px;
    width: 60px;
    background-color: #bdbfc1;
    border-radius: 50%;
    margin-top: 0px;
    text-align: 'center';
    position: relative;
    top: 50%;
    -webkit-transform: perspective(1px) translateY(-50%);
            transform: perspective(1px) translateY(-50%);
}
.dot {
    height: 5px;
    width: 5px;
    background-color: #000000;
    border-radius: 50%;
    margin-top: 0px;
    text-align: 'center';
    display: inline-block;
    position: relative;
    top: 50%;
    -webkit-transform: perspective(1px) translateY(-50%);
            transform: perspective(1px) translateY(-50%);
    margin: 1px;
}

.settlement {
    height: 30px;
    width: 30px;
    border-radius: 50%;
    text-align: 'center';
    display: inline-block;
    position: absolute;
    top: 50%;
    -webkit-transform: perspective(1px) translateY(-50%);
            transform: perspective(1px) translateY(-50%);
    margin: 1px;
  }
  
  .settlementExample {
