#background {
    margin: 5vh;
  }  
#mainDiv{
	font-family: 'Open Sans', sans-serif;
	margin: 0;
	padding: 0;
	width: 100%;
	height: 100%;
	text-align:center;
}
#myBar {
  position: absolute;
  left: 5%;
}

div.button{
  display:inline-block;

	font:normal normal 300 1.3em 'Open Sans';
	text-decoration:none;

	color:rgba(130, 190, 131, 1);
	background-color:transparent;
	border:1px solid rgba(130, 190, 131, 1);
	border-radius:100px;

	padding: .3em 1.2em;
	margin:5px;

	background-size: 200% 100%;
	background-image: -webkit-gradient(linear, left top, right top, color-stop(50%, transparent), color-stop(50%, #e54b4b));
	background-image: -webkit-linear-gradient(left, transparent 50%, #e54b4b 50%);
	background-image: linear-gradient(to right, transparent 50%, #e54b4b 50%);
	-webkit-transition: background-position 1.5s cubic-bezier(0.19, 1, 0.22, 1) .5s, color .5s ease 0s, background-color 2.5s ease;
	transition: background-position 1.5s cubic-bezier(0.19, 1, 0.22, 1) .5s, color .5s ease 0s, background-color 2.5s ease;

  cursor: pointer;
}

div.button:hover{
	color:rgba(255, 255, 255, 1);
	background-color:	#ead974;
	background-position: -100% 100%;
}


#myBar {
  width: 1%;
  height: 30px;
  background-color: #4CAF50;
}
